@charset "utf-8";
/* ZORA PJ core CSS - core.css */

body {
  background-color: var(--main);
}

#header {
  position: absolute;
  width: 100%;
  top: 0;
  transition: 0.7s;
}

#header.fixed {
  position: fixed;
  background-color: var(--main);
  width: 100%;
  top: 0;
  z-index: 2;
  fill: black;
  box-shadow: 0px 0.5px 20px rgba(0, 0, 0, 0.1);
}

#header.fixed a {
  color: black;
}

/* cursor */
.cursor {
  width: 20px;
  height: 20px;
  background-color: #f253b9;
  border-radius: 100%;
  position: fixed;
  z-index: 999;
  pointer-events: none;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

/* header */
.topBar {
  width: 100%;
  height: 30px;
  background-color: var(--black);
  position: relative;
  z-index: 3;
}

.topBar > p {
  color: var(--white);
  font-size: var(--font-size-S);
  line-height: 30px;
  text-align: center;
  width: 100%;
}

/* nav */

.DTnav-wrap {
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  z-index: 3;
}

.nav-L {
  display: flex;
  flex-basis: 50%;
  justify-content: flex-start;
}

.nav-L ul {
  display: flex;
  margin-left: 20px;
}

.nav-L ul li {
  margin: 0 10px;
}

.nav-logo svg {
  width: 115px;
  height: 35px;
  fill: var(--black);
}

.nav-R {
  display: flex;
  flex-basis: 50%;
  justify-content: flex-end;
}

.nav-R ul {
  display: flex;
  margin-right: 20px;
}

.nav-R ul li {
  margin: 0 10px;
}

#DTnav li,
#Mnav li {
  color: var(--black);
  font-family: var(--Atami);
  font-size: var(--font-size-menu);
}

/* footer */
#footer {
  background-color: var(--black);
  padding: 30px;
}

.nav-logo-footer {
  width: 115px;
  height: 35px;
  fill: var(--white);
}

.footer-nav {
  margin-top: 50px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 10vw;
}

.footer-nav__1 {
  margin-bottom: 20px;
  flex-basis: 30%;
}

.footer-nav__2 {
  flex-basis: 30%;
}

.footer-nav ul li a {
  font-size: var(--font-size-S);
  white-space: nowrap;
  color: var(--white);
}

.footer-wrap p {
  color: var(--white);
  margin-top: 20px;
}

/* ëª¨ë°”ì¼ ë©”ë‰´ */
.Mnav-wrap {
  display: flex;
  width: 100%;
  height: 80px;
  align-items: center;
  position: relative;
  z-index: 3;
}

#Mheaderbg {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 2;
  background-color: var(--main);
  transform: translateX(-100%);
  transition: transform 1s;
}

.ham,
.close {
  font-size: var(--font-size-M);
}

.ham {
  color: var(--black);
}

.Mmenu {
  margin-top: min(35vw, 200px);
  padding: 50px;
}

#Mheaderbg li {
  font-size: var(--font-size-L);
  font-family: var(--Coko);
  margin: 50px 0;
}

#Mheaderbg .login {
  position: absolute;
  bottom: 3vw;
  left: 50px;
  font-size: var(--font-size-M);
  font-family: var(--Atami);
}

.Mmenu li:hover a{
  color: #f253b9;
  text-decoration: underline;
}

/* LOGIN */
#Login {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: var(--main);
  z-index: 3;
  transition: opacity 2s;
  opacity: 0;
}

.Login-bg {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
}

.Login-wrap {
  position: absolute;
  z-index: 4;
  box-sizing: border-box;
  padding: 0 20px;
  background-color: var(--main);
  width: 100%;
  height: auto;
}

.Login-wrap .close {
  position: absolute;
  top: 20px;
  right: 20px;
}

.Login-close {
  padding: 10px;
}

.Login-con {
  padding-top: 65px;
}

.Login-con h5 {
  font-size: var(--font-size-M);
  font-family: var(--Coko);
  letter-spacing: 1px;
  text-align: center;
}

.Login-form {
  margin: 80px auto;
  width: min(100vw, 600px);
}

.input > input {
  border: none;
  background-color: transparent;
  width: 100%;
  padding: 20px 0;
  border-bottom: 1px solid black;
}

.Login-con__btn {
  width: 100%;
  padding: 20px 0;
  background-color: var(--black);
  color: var(--white);
  font-family: var(--Atami);
}

.Login-con__name,
.Login-con__pw {
  position: relative;
}

.Login-con__pw {
  margin: 50px 0;
}

.input label {
  font-size: var(--font-size-S);
  font-family: var(--Atami);
  position: absolute;
  left: 0;
}
