#subface {
  position: relative;
}

.subface-wrap {
  height: 100vh;
  width: 100%;
}

.subface-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.subface-bgwrap {
  height: 65vh;
  width: 100%;
  overflow: hidden;
  position: absolute;
  bottom: 0;
}

.subface-bg {
  width: 100%;
  height: 100vh;
  background-color: var(--main);
  border-top-left-radius: 9999px;
  border-top-right-radius: 9999px;
}

.subface-bg img {
  display: block;
  margin: 0 auto;
  padding-top: 50px;
  width: 95px;
  height: 100px;
}

.subface-con {
  text-align: center;
  font-family: var(--Coko);
  position: absolute;
  top: 70vh;
  left: 50%;
  transform: translateX(-50%);
}

.subface-con h1 {
  font-size: var(--font-size-tit);
  margin-bottom: 50px;
}

.subface-con p {
  font-size: var(--font-size-S);
  font-family: var(--Atami);
  line-height: 25px;
  width: min(80vw, 600px);
  margin: 0 auto;
}

.number-btn {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  font-size: var(--font-size-M);
  box-sizing: border-box;
}

.number-btn__1 {
  display: flex;
  box-sizing: border-box;
  border: 1px solid var(--black);
  width: min(85vw, 400px);
  height: 50px;
  margin: 0 auto;
  align-items: center;
  justify-content: space-between;
}

.number-btn__1 span {
  padding: 10px 15px;
}

.number-btn__1 input {
  border: none;
  background-color: var(--main);
  height: 45px;
  width: 80%;
  text-align: center;
}

.number-btn__2 {
  margin: 0 auto;
  height: 50px;
  width: min(85vw, 400px);
  background-color: var(--black);
  color: var(--white);
  margin-top: 10px;
}

.number-btn__2:hover {
  background-color: rgba(0, 0, 0, 0.5);
}

.item {
  padding-top: 300px;
}

.item-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.item-L img {
  width: min(85vw, 600px);
  border-top-left-radius: 9999px;
  border-top-right-radius: 9999px;
  aspect-ratio: 1/1;
  object-fit: cover;
}

.item-R {
  width: min(85vw, 800px);
}

.about h3,
.about h4 {
  font-family: var(--Coko);
  font-size: var(--font-size-S);
}

.about p {
  font-family: var(--Atami);
  font-size: var(--font-size-S);
}

.about {
  border-bottom: 1px dotted #000;
}

.about-1 h3 {
  margin-top: 30px;
  margin-bottom: 20px;
}

.about-2,
.about-3,
.about-4,
.about-5 {
  padding: 20px 0;
}

.about-3 p,
.about-4 p,
.about-5 p {
  margin-top: 20px;
}

.tasting-wrap {
  margin: 50px;
  width: 80%;
  margin: 0 auto;
  font-family: var(--Coko);
}

.tasting-wrap h2 {
  font-size: var(--font-size-M);
  text-align: center;
  margin: 100px;
}

.tasting-con {
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}

.tasting img {
  width: min(35vw, 120px);
  height: min(35vw, 120px);
  border-radius: 50%;
}

.tasting-0,
.tasting-1,
.tasting-2,
.tasting-3 {
  flex-basis: 50%;
}

.tasting-2,
.tasting-3 {
  margin: 50px 0;
}

.tasting-0 h4,
.tasting-1 h4,
.tasting-2 h4,
.tasting-3 h4 {
  margin-top: 10px;
}

/* ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ ë¯¸ë””ì–´ì¿¼ë¦¬ ì‹œì‘ ğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆ */
@media screen and (min-width: 768px) {
  .subface-con p {
    font-size: var(--font-size-M);
  }
  .item-wrap {
    flex-direction: row;
  }

  .item-L,
  .item-R {
    flex-basis: 50%;
    margin: 0 20px;
  }

  .item-L img {
    width: 50vw;
    height: 600px;
    aspect-ratio: 0.5/0.9;
  }

  .tasting-con {
    margin-bottom: 50px;
  }

  .tasting-0,
  .tasting-1,
  .tasting-2,
  .tasting-3 {
    flex-basis: 25%;
  }
}
@media screen and (min-width: 1024px) {
  .item-L{
    text-align: center;
  }

  .item-R{
    max-width: 600px;
    font-size: var(--font-size-M);
  }

  .about h3,
.about h4 {
  font-size: var(--font-size-M);
}

.about p {
  font-size: var(--font-size-M);
}

  .item-L img {
    width: min(85vw, 600px);
    aspect-ratio: 1/1;
    text-align: center;
  }
  
  .tasting img {
    width: min(35vw, 150px);
    height: min(35vw, 150px);
  }
}