#domecon {
  margin: 10vh 20px;
}

.domecon-wrap__main0,
.domecon-wrap__story0,
.domecon-wrap__story1,
.domecon-wrap__impact0,
.domecon-wrap__impact1 {
  display: flex;
  flex-direction: column;
  padding: 20px;
  justify-content: space-around;
}

.domecon-con .src2 {
  width: min(85vw, 600px);
  height: auto;
  padding: 20px;
  box-sizing: border-box;
}

.domecon-img img {
  width: 100%;
  border-top-left-radius: 9999px;
  border-top-right-radius: 9999px;
  aspect-ratio: 1/1;
  object-fit: cover;
}

.domecon-img__cation {
  font-family: var(--Atami);
  font-size: var(--font-size-S);
  position: absolute;
}

.domecon-wrap__main0 .domecon-con,
.domecon-wrap__story0 .domecon-con,
.domecon-wrap__story1 .domecon-con,
.domecon-wrap__impact0 .domecon-con,
.domecon-wrap__impact1 .domecon-con,
.domecon-wrap__impact2 .domecon-con {
  margin-top: 5vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  justify-content: flex-end;
}

.domecon-wrap__impact2 .domecon-con {
  text-align: center;
}

.svg_oneshool {
  width: 95px;
  height: 100px;
  padding: 10px;
}

.domecon-con__tit {
  font-size: var(--font-size-L);
  font-family: var(--Coko);
}

.domecon-con__text {
  font-family: var(--Atami);
  font-size: var(--font-size-S);
  margin-top: 3vw;
}

.domecon-con__btn {
  display: block;
  font-size: var(--font-size-M);
  margin-top: 3vw;
}

.domecon-con__btn::after {
  background-color: var(--black);
}

/* ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ğŸ‘‰ ë¯¸ë””ì–´ì¿¼ë¦¬ ì‹œì‘ ğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆğŸ‘ˆ */
@media screen and (min-width: 768px) {
  .domecon-wrap__main0,
  .domecon-wrap__story0,
  .domecon-wrap__story1,
  .domecon-wrap__impact0 {
    flex-direction: row;
    align-items: flex-end;
  }

  .domecon-wrap__impact1 {
    flex-direction: row-reverse;
    align-items: flex-end;
  }

  .domecon-img img {
    width: 40vw;
    height: 600px;
    aspect-ratio: 0.5/0.9;
  }

  .domecon-con__text {
    font-size: var(--font-size-M);
    line-height: 28px;
    max-width: 800px;
    margin-top: 3vw;
  }

  .domecon-wrap__main0 .domecon-con,
  .domecon-wrap__story0 .domecon-con,
  .domecon-wrap__story1 .domecon-con{
    align-items: flex-start;
    text-align: left;
    margin-left: 20px;
    margin-top: 0;
    height: 600px;
  }

  .domecon-wrap__impact0 .domecon-con{
    text-align: left;
    margin-left: 20px;
  }

  .domecon-wrap__impact1 .domecon-con {
    margin-right: 20px;

  }

  .domecon-wrap__impact0 .src2{
    display: none;
  }
}

@media screen and (min-width: 1024px) {

  .domecon-wrap__main0 .domecon-img img,
  .domecon-wrap__story0 .domecon-img img,
  .domecon-wrap__story1 .domecon-img img,
  .domecon-wrap__impact0 .domecon-img img,
  .domecon-wrap__impact1 .domecon-img img {
    width: min(50vw,800px);
    height: 100%;
    aspect-ratio: 1/1;
  }
}

@media screen and (min-width: 1600px) {

  .domecon-wrap__impact0 .src2{
    display: block;
    width: 800px;
  }
}
