@charset "utf-8";
/* ZORA PJ reset CSS - reset.css */

/* ê¸°ë³¸ ì´ˆê¸°í™” */
html, body, h1, h2, h3, h4, h5, p, ul, ol, button, form, input{
    margin: 0;
    padding: 0;
}

/* body{
    overflow-x: hidden;
} */

/* ë¦¬ìŠ¤íŠ¸ ì´ˆê¸°í™” */
ul, ol{
    list-style: none;
}

/* root font */
html{
    font-size: 10px;
}

/* a */
a{
    text-decoration: none;
    color: var(--black);
    cursor: none;
}

address{
    margin: 0;
    padding: 0;
}

/* IR */
.ir{
    display: block;
    width: 0;
    height: 0;
    overflow: hidden;
}

button{
    border: none;
    background: transparent;
    cursor: none;
}

a:focus, button:focus{
    border: none;
}
input:focus{
    outline: none;
}
